    height: 50px;
    border-radius: 50%;
    object-fit: cover;
    border: 2px solid rgb(149, 149, 149);
}

.userprofileinfo{
    margin: 0 20px ;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    object-fit: cover;
    border: 2px solid rgb(149, 149, 149);

}

.userinformationphoto{
    height: auto;
    width: auto;
    width: 260px;
    height: 260px;
